import React from 'react';

function Doc() {

  return (
    <div className="container w-100">

      <h1>Documentação do Plugin QrCode</h1>
      <hr></hr>
      <span>Para utilizar basta seguir os passos que vou te mostrar no link abaixo:</span><br />
        <span ng-if="tab.tabHref">
          <a href="https://github.com/Wilkor/doc-plugin-qrcode#readme" class="tab-href" target="_blank" style={{ color: 'black', textDecoration: 'nome' }}>Click here! <i class="icon-external-link icon icon-xss mb2"></i></a>
        </span>
    </div>
  );

}


export default Doc;